.Home {
  display: flex;
  justify-content: space-around;

  padding: 20px 60px 20px 60px;
}

/* 레이아웃 */
.Home__left {
  width: 20%;
  padding: 10px;
}

.Home__left > div {
  padding: 20px;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 2px var(--grey);
}

.Home__left > div > div {
  margin-bottom: 20px;
}

.Home__right {
  width: 80%;
  padding: 10px;
}

.Home__right > div {
  padding: 20px;

  border-radius: 5px;
  box-shadow: 1px 1px 4px 2px var(--grey);
}

.Home__right > div > div {
  margin-bottom: 20px;
}

/* 컴포넌트 */
.Home__profile-img {
  width: 100%;
  height: 0;
  padding-bottom: 100%;

  border-radius: 100%;
  overflow: hidden;
}

.Home__profile-img > img {
  width: 100%;
}

.Home__profile-content {
  padding: 0px 10px 0px 10px;
}

.Home__profile-content > h3 {
  text-align: center;
}