.Articles {
  display: flex;
  padding: 20px 60px 20px 60px;
}

/* 레이아웃 */
.Articles__left {
  width: 20%;
  padding: 10px;
}

.Articles__right {
  width: 80%;

  margin-top: 30px;
  padding: 0px 50px 10px 50px;

  border-radius: 10px;
  box-shadow: 2px 2px 4px 2px var(--grey);

  background-color: rgba(250, 250, 250, 100);
}

.Articles__Category {
  display: flex;
  flex-direction: column;
}

.Articles__Category > div {
  margin: 5px;
}

.Articles__Category > div > span {
  display: flex;
  flex-direction: column;

  margin-left: 20px;
}